import React from "react";
import { connect } from "react-redux";
import ReactHtmlParser from "react-html-parser";
import { checkDistance } from "../../actions";
import "./style.css";
import CircularProgress from "@material-ui/core/CircularProgress";

class Picker extends React.Component {
  state = { selectedFrom: "", To: "", distance: "" };

  componentDidUpdate() {
    if (this.props.distance.state === undefined) {
      document.getElementById("distance").innerHTML = "";
    } else {
      document.getElementById("distance").innerHTML = `${JSON.stringify(
        this.props.distance.state
      )}km`;

      document.getElementById("button1").style.display = "none";
      document.getElementById("button2").style.display = "flex";
    }
  }
  fetch = () => {
    let from = "";
    try {
      from = `<option value="" />`;
      this.props.cities.state.forEach((element, index) => {
        from += `<option value=${index}>${element}</option>`;
      });
      document.getElementById("button1").style.display = "none";
      document.getElementById("button2").style.display = "flex";
      return ReactHtmlParser(from);
    } catch (err) {}
  };

  onChangeFrom = e => {
    this.setState({ selectedFrom: e.target.value });
  };

  onChangeTo = e => {
    this.setState({ selectedTo: e.target.value });
  };

  onClick = () => {
    document.getElementById("distance").innerHTML = "";
    document.getElementById("button1").style.display = "flex";
    document.getElementById("button2").style.display = "none";
    this.props.checkDistance(this.state.selectedFrom, this.state.selectedTo);
  };

  render() {
    return (
      <div className="Picker">
        <div id="title">
          Czy Ciebie też irytuje czytanie tej ogromnej tabelki z wykazem
          odległości między stacjami od SKM?
        </div>
        <div id="main">
          Od:
          <div id="from">
            <select onChange={this.onChangeFrom}>{this.fetch()}</select>
          </div>
          Do:
          <div id="to">
            <select onChange={this.onChangeTo}>{this.fetch()}</select>
          </div>
          <div id="distance" />
        </div>
        <div id="submit">
          <div id="button1" className="button" onClick={this.onClick}>
            <CircularProgress disableShrink />
          </div>
          <div id="button2" className="button" onClick={this.onClick}>
            Sprawdź
          </div>
        </div>
        <footer>© Copyright Mikołaj Sojka</footer>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    cities: state.cities,
    distance: state.distance
  };
};

export default connect(mapStateToProps, { checkDistance })(Picker);
